import React from 'react';

export default class PageTextArea extends React.Component {
  render() {
    return (
      <div className='textarea-container grid-block small-2-up'>
        <div className='avatar small-3 align-center'>
          <img src={this.props.avatarUrl} />
        </div>
        <div className='grid-block small-20'>
          <textarea
            placeholder={this.props.placeholder}
            ref='contribution'
            value={this.props.value}
            onChange={this.props.onChange} />
        </div>
      </div>
    );
  }
}

PageTextArea.propTypes = {
  avatarUrl: React.PropTypes.string,
  placeholder: React.PropTypes.string,
  value: React.PropTypes.string,
  onChange: React.PropTypes.func
};

PageTextArea.defaultProps = {
  avatarUrl: require('../../assets/images/avatar.png'),
  placeholder: 'What happens next?',
  value: null
};


