import React from 'react';
import classNames from 'classnames';

import Highlight from './Highlight';
import LikeButton from './LikeButton';
import UserProfile from '../../support/UserProfile';

export default class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      content: this.props.content,
      creator: this.props.creator,
      image: this.props.image,
      refPageId: this.props.refPageId,
      commentCount: this.props.commentCount,
      isCommentSelected: false
    };
    this.handleClickComment = this.handleClickComment.bind(this);
  }

  contentText() {
    const content = this.state.content;
    if (this.props.aids && this.props.aids.length) {
      const children = this.props.aids[0].content.tags.reduce((array, tag) => {
        const remain = array[array.length - 1];
        const taggedText = {
          content: content.slice(tag.startPos, tag.endPos + 1),
          startPos: tag.startPos,
          endPos: tag.endPos,
          tag: tag
        };
        if (remain.startPos >= tag.startPos) {
          array.splice(array.length - 1, 0, taggedText);
        } else {
          const left = {
            content: remain.content.slice(0, tag.startPos - remain.startPos),
            startPos: remain.startPos,
            endPos: tag.startPos - 1,
          };

          const right = {
            content: remain.content.slice(tag.endPos + 1 - remain.startPos),
            startPos: tag.endPos + 1,
            endPos: remain.endPos
          };
          array.splice(array.length - 1, 0, left);
          array.splice(array.length - 1, 0, taggedText);
          array.splice(array.length - 1, 1, right);
        };
        return array;
      }, [{content, startPos: 0, endPos: content.length}]).reduce((sum, element, i, array) => {
        if (!element.tag) {
          sum.push(element.content);
          return sum;
        } else {
          if (i > 0 && element.startPos >= array[i - 1].startPos && element.endPos <= array[i - 1].endPos) {
            let last = sum[sum.length - 1];
            const children = [
              <Highlight
                writeAssistOn={this.props.writeAssistOn}
                tag={element.tag}
                key={i}
              >{element.content}</Highlight>,
              <Highlight
                writeAssistOn={this.props.writeAssistOn}
                tag={array[i - 1].tag}
                key={i - 1}
              >
                {last.props.children.slice(array[i - 1].endPos - element.endPos)}
              </Highlight>
            ];
            sum[sum.length - 1] = (<span
              writeAssistOn={this.props.writeAssistOn}
              tag={array[i-1].tag}
              key={i}
            >{children}</span>);
            return sum;
          } else {
            sum.push(<Highlight
              writeAssistOn={this.props.writeAssistOn}
              tag={element.tag}
              key={i}>{element.content}</Highlight>,);
            return sum;
          };
        };
      }, []);
      return (<p>{children}</p>);
    } else {
      return (<p>{this.state.content}</p>);
    };
  }

  imageContent() {
    if (this.state.image && this.state.refPageId) {
      return (<div><img src={this.state.image._url} /></div>);
    }
  }

  handleClickComment() {
    this.props.onClickComment(this.props.objectId);
    this.setState({isCommentSelected: !this.state.isCommentSelected});
  }

  deselectComment() {
    this.setState({isCommentSelected: false});
  }

  incrementCommentCount() {
    this.setState({commentCount: this.state.commentCount + 1});
  }

  render() {
    let commentButtonClasses = classNames({
      'button': true,
      'comment-button': true,
      'active': this.state.isCommentSelected
    });

    let commentButton;
    if (this.props.onClickComment) {
      commentButton = (
        <div className={commentButtonClasses} onClick={this.handleClickComment}>
          <i className="fa fa-comment"></i>
          <span className="comment-count">{this.state.commentCount}</span>
        </div>
      );
    }

    let creator = this.state.creator;

    return (
      <div className='page small-24 large-24 grid-block'>
        <div className="line"></div>
        <div className="avatar small-3">
          <img src={UserProfile.getAvatar(creator)} title={UserProfile.getDisplayName(creator)}/>
        </div>
        {commentButton}
        <LikeButton {...this.props} />
        <div className="content small-18">
          {this.contentText()}
          {this.imageContent()}
        </div>
      </div>
    );
  }
}

Page.defaultProps = { commentCount: 0 };

Page.propTypes = {
  onClickComment: React.PropTypes.func
};


